<template>
  <div v-if="loading" class="">
    <LoadSkeletonType></LoadSkeletonType>
  </div>
  <div v-else class="content">
    <div class="h-36 movie-type-list grid grid-cols-3 md:grid-cols-6 gap-3">
      <div
        v-for="(item, index) in movie_tags"
        :key="index"
        v-bind:style="{ backgroundImage: 'url(' + item.back_image + ')' }"
        @click="typeRef(item.title)"
        class="h-full flex items-center justify-center w-full rounded-lg py-4 px-4 mx-auto text-center bg-cover bg-center bg-no-repeat dark:bg-gray-700 bg-gray-700 bg-blend-multiply"
      >
        <h1
          :class="[item.title === movie_type ? 'active' : '']"
          class="text-xl relative font-extrabold tracking-tight leading-none md:text-4xl lg:text-5xl dark:text-white text-white"
        >
          {{ item.title }}
        </h1>
      </div>
    </div>
    <div class="tv-content-list">
      <div class="content-video-title flex items-center justify-between py-6">
        <div class="content-video-title-lift flex items-center justify-between">
          <div class="title-icon">
            <svg
              class="w-6 h-6 text-gray-800 dark:text-white"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              fill="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                fill-rule="evenodd"
                d="M5 10.2c.6 0 1 .4 1 1v.2l6 3.5 6-3.5v-.2a1 1 0 1 1 2 0v.8c0 .4-.2.7-.5.9l-7 4a1 1 0 0 1-1 0l-7-4A1 1 0 0 1 4 12v-.8c0-.6.5-1 1-1Zm0 5c.6 0 1 .4 1 1v.2l6 3.4 6-3.4V16a1 1 0 1 1 2 0v.9c0 .3-.2.6-.5.8l-7 4a1 1 0 0 1-1 0l-7-4A1 1 0 0 1 4 17v-1c0-.5.4-1 1-1Z"
                clip-rule="evenodd"
              />
              <path
                d="M12.5 2.1a1 1 0 0 0-1 0l-7 4a1 1 0 0 0 0 1.8l7 4c.3.2.7.2 1 0l7-4a1 1 0 0 0 0-1.7l-7-4Z"
              />
            </svg>
          </div>
          <div class="title-text font-bold text-gray-800 dark:text-white">
            资源
          </div>
        </div>
        <router-link
          :to="{ path: '/list/电影', query: { video_tag: movie_type } }"
          class="content-video-title-rigth font-bold text-gray-800 dark:text-white"
        >
          更多
        </router-link>
      </div>
      <div class="content-video-list grid grid-cols-3 md:grid-cols-8 gap-3">
        <router-link
          :to="'/watch/' + line.ID"
          v-for="(line, number) in data"
          :key="number"
          class="video-item-card h-60 brightness-50 w-full rounded-sm object-cover object-center"
        >
          <img
            loading="lazy"
            class="video-item-card-image rounded-sm brightness-50"
            :src="line.image"
            alt=""
          />
          <div
            class="video-item-card-title mt-2 text-sm font-bold text-gray-800 dark:text-white"
          >
            {{ line.title }}
          </div>
          <div
            class="video-item-card-heard px-1 py-1 w-full absolute top-0 left-0"
          >
            <div
              class="item-card-heard-content flex items-center justify-between"
            >
              <div class="item-card-heard-speed text-sm text-gray-200">
                {{ COMMON.getSpeed(line) }}
              </div>
              <div class="item-card-heard-score text-sm text-yellow-300">
                {{ line.score + "分" }}
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast } from "flowbite-vue";
import { ref, onMounted, getCurrentInstance } from "vue";
import LoadSkeletonType from "../widgets/LoadSkeletonType.vue";

export default {
  components: {
    LoadSkeletonType,
  },
  setup() {
    const loading = ref(true);
    const toast = useToast();
    const movie_type = ref(null);
    const movie_tags = ref(null);
    const data = ref(null);
    const error = ref(null);
    const { proxy } = getCurrentInstance();

    movie_tags.value = proxy.COMMON.movie_tags;
    movie_type.value = movie_tags.value[0].title;

    function fetchData() {
      movie_type.value =
        movie_type.value === "最新" ? "剧情" : movie_type.value;
      let api = `${proxy.COMMON.apiUrl}/v1/api/video/jojo/type/tag?type=电影&tag=${movie_type.value}&page=1&size=24`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            data.value = json.data;
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          loading.value = false;
        });
    }

    onMounted(() => {
      document.title = "电影";
      fetchData();
    });

    const typeRef = async (typeData) => {
      movie_type.value = typeData;
      toast.add({
        type: "success",
        time: 3000,
        text: "获取资源中!",
      });
      fetchData();
    };
    return {
      loading,
      toast,
      movie_type,
      movie_tags,
      data,
      typeRef,
    };
  },
  methods: {},
};
</script>

